<template>
  <div id="box">
    <Row class="list list1">
        <Col span="24" style="position: relative;">
        	  <div class="a" @click="toTest">
        	  	<p class="title">{{title1}}</p>
        	    <p class="cont">天赋测试</p>
        	    <p class="cont">XXXXXXXXXXXXXXXXX</p>
        	    <span class="glyphicon glyphicon-menu-right" aria-hidden="true"></span>
        	  </div>
        </Col>
    </Row>
    <!--<Row class="list list2">
        <Col span="24" style="position: relative;">
        	  <router-link to="/" class="a">
        	  	<p class="title">{{title2}}</p>
        	    <p class="cont">天赋测试</p>
        	    <p class="cont">XXXXXXXXXXXXXXXXX</p>
        	    <span class="glyphicon glyphicon-menu-right" aria-hidden="true"></span>
        	  </router-link>
        </Col>
    </Row>
    <Row class="list list3">
        <Col span="24" style="position: relative;">
        	  <router-link to="/" class="a">
        	  	<p class="title">{{title3}}</p>
        	    <p class="cont">天赋测试</p>
        	    <p class="cont">XXXXXXXXXXXXXXXXX</p>
        	    <span class="glyphicon glyphicon-menu-right" aria-hidden="true"></span>
        	  </router-link>
        </Col>
    </Row>-->
    <div class="nav">
    	  <router-link to="/" id="test" style="border-right: 1px solid #fff;">
    	  	<p class="glyphicon glyphicon-list icon" aria-hidden="true"></p>
    	  	<p>测试</p>
    	  </router-link>
    	  <router-link to="/my" id="me">
    	  	<p class="glyphicon glyphicon-user icon" aria-hidden="true"></p>
    	  	<p>我</p>
    	  </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'testList',
    data () {
      return {
        title1: '红黄性格属性测试',
        title2: '执行力测试',
        title3: '行动力测试'
      }
    },
    methods: {
      toTest: function () {
        this.$router.push({
          path: '/startTest',
          query: {title: this.title1}
        })
      }
    }
  }
</script>

<style scoped>
  .list{
  	height: 8rem;
  	padding-left: 1.5rem;
  }
  .list1{
  	background-color: lightseagreen;
  }
  .list2{
  	background-color: skyblue;
  }
  .list3{
  	background-color: forestgreen;
  }
  .title{
  	font-size: 21px;
  	margin-top: 1rem;
  }
  .cont{
  	margin-top: -0.5rem;
  	font-weight: 400;
  }
  .a{
  	width: 100%;
  	height: 100%;
  	display: block;
  	text-decoration: none;
  	color: #fff;
  }
  span{
  	position: absolute;
  	right: 1.5rem;
  	top: 3rem;
  	font-size: 20px;
  	font-weight: 700;
  }
  .nav{
  	position: absolute;
  	left: 0;
  	bottom: 0;
  	height: 5rem;
  	width: 100%;
  	background-color: darkorange;
  }
  .nav a{
  	width: 50%;
  	height: 100%;
  	text-decoration: none;
  	color: #fff;
  	display: block;
  	float: left;
  	text-align: center;
  }
  .nav p{
  	margin-top: -0.2rem;
  	font-size: 18px;
  }
  .nav .icon{
  	font-size: 20px;
  	margin-top: 0.45rem;
  }
</style>



