<template>
  <div id="box">
    <p class="p1">根据您的测试结果，你属于：<span class="result">{{result}}</span></p>
    <div class="red">
    	  <ul>
    	    <li>• 很容易分心，可能在从停车处到家门的途中“走失”</li>
    	    <li>• 热爱新东西，最好每天下班回家都能取道不同的路。</li>
    	    <li>• 不为任务做计划，只是等待并观望需要做些什么；人们指责自己没有计划性，其实你知道这就是自己的特点。</li>
    	    <li>• 对于最后期限，需要依靠最后一分钟的冲刺；一般你总是能按期限完成，尽管在此过程中自己可能把大家都急的发疯。</li>
    	    <li>• 虽然你喜欢事物有序，但不相信“整洁很重要”；重要的是创造力、自发性和反应程度。</li>
    	    <li>• 把大多数工作转化为玩乐；如果工作没有乐趣，大概它就不值得去做。</li>
    	    <li>• 经常在谈话中切换话题，新的话题可能是刚进入大脑或房间的任何事情。</li>
    	    <li>• 不喜欢被事情钉死，更愿意保持多一些选择。</li>
    	    <li>• 常常趋向于使事情不那么确定，但并不总是如此，依情况而定。</li>
    	  </ul>
    </div>
    <div class="yellow">
      <ul>
        <li>• 总是在等待那些看来从不守时的人。</li>
        <li>• 每件东西都应有它放的地方，而且每件东西都应该放在它应该放的地方。</li>
        <li>• “知道”如果我们每个人都能（在应该完成的时候）完成我们所应该完成的事，那么世界将会变得更美好。</li>
        <li>• 早晨睁开眼睛就很清楚自己的一天该怎样安排；自己有一个计划，如果事情不能按计划进行，就会有挫败感。</li>
        <li>• 告诉周围的人，不喜欢总有出乎意料的事情发生。</li>
        <li>• 讲究秩序，对办公桌上的问句，档案夹中的文件以及墙上的挂件都有专门的系统进行整理。</li>
        <li>• 喜欢有始有终的完成工作从而从清单中钩除，即使自己知道以后还得返工来更正一些细节性的东西。</li>
      </ul>
    </div>
    <Button type="ghost" long @click="makeChoose(0)">我更倾向红色</Button>
    <Button type="ghost" long @click="makeChoose(1)">我更倾向黄色</Button>
    <Button type="ghost" long @click="makeChoose(2)">我不确定</Button>
    <div id="jump" v-if="show">
      <h4>做好选择了么，即将跳到结果页面</h4>
      <div class="choose">
        <button @click="not">还没选好</button>
        <button @click="yeah(this.num)">选好了</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'RYresult',
    data () {
      return {
        red: '',
        yellow: '',
        jump: '',
        result: '',
        show: false,
        num: ''
      }
    },
    methods: {
      showResult: function () {
        this.red = this.$route.query.redScore
        this.yellow = this.$route.query.yellowScore
        this.jump = this.$route.query.jumpScore
        this.getResult(this.red, this.yellow)
      },
      getResult: function (red, yellow) {
        var lala = red - yellow
        if (lala >= 0) {
          this.result = '红色'
        } else {
          this.result = '黄色'
        }
      },
      makeChoose: function (num) {
        this.show = true
        this.num = num
      },
      not: function () {
        this.show = false
      },
      yeah: function () {
        this.$router.push({
          path: '/testResultRY',
          query: {result: this.result, lala: this.num}
        })
      }
    },
    mounted: function () {
      this.showResult()
    }
  }
</script>

<style scoped>
  .p1{
  	padding-left: 1.5rem;
  	margin-top: 1.5rem;
  }
  .red{
  	margin-top: 1rem;
  	width: 100%;
  	height: 18rem;
  	background-color: indianred;
  	padding-left: 1.5rem;
  	padding-right: 1rem;
  	padding-top: 0.5rem;
  	overflow: auto;
  	color: #fff;
  }
  .red li{
  	margin: 0.25rem 0;
  }
  .yellow{
  	margin-top: 1rem;
  	margin-bottom: 1rem;
  	width: 100%;
  	height: 18rem;
  	background-color: gold;
  	padding-left: 1.5rem;
  	padding-right: 1rem;
  	padding-top: 0.5rem;
  	overflow: auto;
  }
  .yellow li{
  	margin: 0.25rem 0;
  }
  Button{
  	margin: 0.25rem 0;
  	font-size: 17px;
  }
  #jump{
  	width: 28rem;
  	height: 12rem;
  	background-color: #e1e1e1;
  	position: absolute;
  	top: 50%;
  	left: 50%;
  	margin-top: -10rem;
  	margin-left: -14rem;
  	border: 1px solid #b1b1b1;
  	border-radius: 10px;
  }
  #jump h4{
  	text-align: center;
  	margin-top: 3rem;
  }
  #jump .choose{
  	width: 100%;
  	height: 5rem;
  	margin-top: 2rem;
  	border-top: 1px solid #101010;
  }
  .choose button{
  	border: none;
  	outline: none;
  	background-color: transparent;
  	width: 49.3%;
  	height: 100%;
  	box-sizing: border-box;
  }
  .choose button:nth-child(1){
  	border-right: 1px solid #101010;
  }
</style>



