<template>
  <div id="box">
    <div id="img"></div>
    <div class="title">
      <h2>{{title}}</h2>
      <p>看看你本能对待外部世界的习惯态度</p>
    </div>
    <div class="info">
    	  <span>共9题</span>
    	  <span>约5分钟</span>
    	  <span>1页报告</span>
    </div>
    <div class="nav">
    	  <a id="relation">联系客服</a>
    	  <a id="startTest" @click="toQuestion">开始测试</a>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'startTest',
    data () {
      return {
        title: ''
      }
    },
    methods: {
      getTitle: function () {
        this.title = this.$route.query.title
        console.log(this.title)
      },
      toQuestion: function () {
        this.$router.push({
          path: '/question'
        })
      }
    },
    mounted: function () {
      this.title = this.$route.query.title
    }
  }
</script>

<style scoped>
  #img{
  	width: 100%;
  	height: 13rem;
  	background-color: grey;
  }
  .title{
  	height: 8rem;
  	border-bottom: 1px solid #333;
  	text-align: center;
  }
  .title p{
  	font-size: 18px;
  	margin-top: 1.5rem;
  }
  .info span{
  	float: left;
  	margin-top: 0.75rem;
  }
  .info span:nth-child(1){
  	margin-left: 0.75rem;
  }
  .info span:nth-child(2){
  	margin-left: 10rem;
  }
  .info span:nth-child(3){
  	float: right;
  	margin-right: 0.75rem;
  }
  .nav{
  	position: absolute;
  	left: 0;
  	bottom: 0;
  	width: 100%;
  	height: 6rem;
  }
  a{
  	display: block;
  	float: left;
  	height: 100%;
  	line-height: 6rem;
  	text-align: center;
  	font-size: 20px;
  	text-decoration: none;
  }
  #relation{
  	width: 40%;
  	color: #666;
  }
  #startTest{
  	width: 60%;
  	background-color: dodgerblue;
  	color: #fff;
  }
</style>



