<template>
  <div id="box">
    <div id="question">
    	  {{id+1}}. {{question}}
      <span id="num">{{id+1}}/{{length}}</span>
    </div>
    <button id="option1" class="option" @touchstart="scoreOption1" score="0">{{option1}}</button>
    <button id="option2" class="option" @touchstart="scoreOption2" score="1">{{option2}}</button>
    <Button type="text" id="next" v-if="jump" @click="toNext" score="2">跳过</Button>
    <Button type="ghost" id="brfore" v-if="back" @click="getBack">上一题</Button>
    <Button type="ghost" id="finish" v-if="finish" @click="getScore">完成测试</Button>
  </div>
</template>

<script>
  import $ from 'jquery'
  
  export default {
    name: 'question',
    data () {
      return {
        id: 0,
        option1: '',
        option2: '',
        question: '',
        finish: false,
        questions: '',
        back: false,
        jump: true,
        redScore: 0,
        yellowScore: 0,
        jumpScore: 0,
        i: 0,
        choose: [],
        flag: true,
        length: ''
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData: function () {
        var _this = this
        $.ajax({
          type: 'get',
          url: '../static/data.json',
          success: function (data) {
            _this.questions = data.questions
            _this.length = data.questions.length
            _this.question = data.questions[0].question
            _this.option1 = data.questions[0].A
            _this.option2 = data.questions[0].B
          }
        })
      },
      changeInner: function (num) {
        // 为了改变题目和选项内容和标记颜色
        this.back = true
        var inx = this.id
        if (!this.choose[inx + 1]) {
          if (inx + 1 < this.length) {
            this.flag = true
            $('#option1').removeClass('current')
            $('#option2').removeClass('current')
          } else if (inx + 1 === this.length) {
            this.flag = false
            if (num === 0) {
              this.choose[this.length - 1].op = 0
              this.choose[this.length - 1].score = this.questions[this.length - 1].opA
              $('#option1').addClass('current')
              $('#option2').removeClass('current')
            } else if (num === 1) {
              this.choose[this.length - 1].op = 1
              this.choose[this.length - 1].score = this.questions[this.length - 1].opB
              $('#option2').addClass('current')
              $('#option1').removeClass('current')
            }
          }
        } else if (this.choose[inx] + 1) {
          this.flag = false
          console.log(this.choose[inx])
          console.log(this.choose[inx + 1])
          if (this.choose[inx + 1].op === 0) {
            $('#option1').addClass('current')
            $('#option2').removeClass('current')
          } else if (this.choose[inx + 1].op === 1) {
            $('#option2').addClass('current')
            $('#option1').removeClass('current')
          } else if (this.choose[inx + 1].op === 2) {
            $('#option1').removeClass('current')
            $('#option2').removeClass('current')
          }
        }
        if (inx === this.length - 1) {
          this.finish = true
          this.jump = false
          inx = this.length - 2
        } else {
          this.finish = false
          this.jump = true
          this.id = this.questions[inx].id
        }
        this.question = this.questions[inx + 1].question
        this.option1 = this.questions[inx + 1].A
        this.option2 = this.questions[inx + 1].B
        var op1 = this.questions[inx + 1].opA
        var op2 = this.questions[inx + 1].opB
        $('#option1').attr('score', op1)
        $('#option2').attr('score', op2)
        if (this.flag) {
          $('#option1').removeClass('current')
          $('#option2').removeClass('current')
        }
      },
      getBack: function () {
        var inx = this.id - 1
        this.question = this.questions[inx].question
        this.option1 = this.questions[inx].A
        this.option2 = this.questions[inx].B
        this.id = inx
        if (this.id === 0) {
          this.back = false
          this.jump = true
        }
        if (this.id < this.length) {
          this.finish = false
          this.jump = true
        } else {
          this.back = true
          this.jump = false
        }
        this.mark(inx)
      },
      scoreOption1: function () {
        var score = $('#option1').attr('score')
        var choose = this.choose
        var idx = this.id
        var A = {
          idx: idx,
          op: 0,
          score: parseInt(score)
        }
        console.log(choose)
        if (this.id < this.length - 1) {
          choose[idx] = A
          this.changeInner()
        } else if (this.id === this.length - 1) {
          if (this.i === 0) {
            choose[idx] = A
            this.i++
          }
          this.changeInner(0)
        }
      },
      scoreOption2: function () {
        var score = $('#option2').attr('score')
        var choose = this.choose
        var idx = this.id
        var A = {
          idx: idx,
          op: 1,
          score: parseInt(score)
        }
        console.log(choose)
        if (this.id < this.length - 1) {
          choose[idx] = A
          this.changeInner()
        } else if (this.id === this.length - 1) {
          if (this.i === 0) {
            choose[idx] = A
            this.i++
          }
          this.changeInner(1)
        }
      },
      mark: function (id) {
        console.log(id)
        if (id || id === 0) {
          var arr = this.choose
          console.log(this.choose)
          var op = arr[id].op
          if (op === 0) {
            $('#option1').addClass('current')
            $('#option2').removeClass('current')
          } else if (op === 1) {
            $('#option2').addClass('current')
            $('#option1').removeClass('current')
          } else if (op === 2) {
            $('#option1').removeClass('current')
            $('#option2').removeClass('current')
          }
        }
      },
      toNext: function () {
        var choose = this.choose
        var idx = this.id
        var A = {
          idx: idx,
          op: 2,
          score: 2
        }
        console.log(this.id)
        console.log(choose)
        if (!$('#option1').hasClass('current') && !$('#option2').hasClass('current')) {
          if (this.id < this.length - 1) {
            choose[idx] = A
          } else if (this.id === this.length - 1) {
            if (this.i === 0) {
              choose[idx] = A
              this.i++
            }
          }
        }
        this.changeInner()
      },
      getScore: function () {
        var result = this.choose
        console.log(result)
        for (var i = 0; i < result.length; i++) {
          var score = result[i].score
          console.log(score)
          switch (score) {
            case 0:
              this.redScore += 1
              break
            case 1:
              this.yellowScore += 1
              break
            case 2:
              this.jumpScore += 1
              break
          }
        }
        console.log(this.redScore + '     红分')
        console.log(this.yellowScore + '     黄分')
        console.log(this.jumpScore + '     跳过')
        this.toRYresult()
        this.redScore = this.yellowScore = 0
      },
      toRYresult: function () {
        this.$router.push({
          path: '/RYresult',
          query: {redScore: this.redScore, yellowScore: this.yellowScore, jumpScore: this.jumpScore}
        })
      }
    }
  }
</script>

<style scoped>
  #question{
  	width: 100%;
  	min-height: 9rem;
  	margin-top: 2rem;
  	line-height: 3rem;
  	font-size: 16px;
  	padding-left: 3rem;
  	padding-right: 4rem;
  	position: relative;
  	word-break: break-all;
  }
  button{
  	display: block;
  	width: 30rem;
  	min-height: 4rem;
  	margin: 3rem auto;
  	font-size: 14px;
  	word-break: break-all;
  }
  .option{
  	width: 30rem;
  	min-height: 5rem;
  	padding: 0 1rem;
  	outline: 0;
  	border: 1px solid #dddee1;
  	color: #495060;
  	background-color: transparent;
  	font-weight: 400;
  	border-radius: 4px;
  	word-break: break-all;
  	line-height: 1.5;
  }
  .current{
  	color: #fff;
    background-color: #2d8cf0;
    border-color: #2d8cf0;
  }
  #option1{
  	
  }
  #option2{
  	
  }
  #next{
  	font-size: 14px;
  }
  #num{
  	position: absolute;
  	font-size: 16px;
  	right: 1rem;
  	top: 0rem;
  	color: #555;
  }
</style>



