<script>
  import $ from 'jquery'
  
  export default {
    name: 'testResulrRY',
    data () {
      return {
        result: '',
        choose: '',
        show: '',
        ratio: '',
        isRed: true,
        isYellow: true
      }
    },
    mounted () {
      var canvas = document.getElementById('canvas')
      var ctx = canvas.getContext('2d')
      var data = [
        {value: '.48', color: 'indianred'},
        {value: '.52', color: 'gold'}
      ]
      var tempAngle = -90
      for (var i = 0; i < data.length; i++) {
        ctx.beginPath()
        ctx.moveTo(50, 50)
        ctx.fillStyle = data[i].color
        var angle = data[i].value * 360
        var startAngle = tempAngle * Math.PI / 180
        var endAngle = (tempAngle + angle) * Math.PI / 180
        ctx.arc(50, 50, 50, startAngle, endAngle)
        ctx.fill()
        tempAngle += angle
      }
      this.result = this.$route.query.result
      this.choose = this.$route.query.lala
      if (this.choose === 0) {
        this.show = '红色性格'
        $('.result span').css('color', 'indianred')
        this.isRed = true
        this.isYellow = false
        this.ratio = '48%'
      } else if (this.choose === 1) {
        this.show = '黄色性格'
        $('.result span').css('color', 'gold')
        this.isRed = false
        this.isYellow = true
        this.ratio = '52%'
      } else if (this.choose === 2) {
        this.show = this.result + '性格'
        if (this.result === '红色') {
          $('.result span').css('color', 'indianred')
          this.isRed = true
          this.isYellow = false
          this.ratio = '48%'
        } else if (this.result === '黄色') {
          $('.result span').css('color', 'gold')
          this.isRed = false
          this.isYellow = true
          this.ratio = '52%'
        }
      }
    },
    methods: {
      again: function () {
        this.$router.push({
          path: '/question'
        })
      }
    }
  }
</script>

