import Vue from 'vue'
import Router from 'vue-router'
import testList from '@/components/testList'
import my from '@/components/my'
import startTest from '@/components/startTest'
import question from '@/components/question'
import RYresult from '@/components/RYresult'
import testResultRY from '@/components/testResultRY'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'testList',
      component: testList
    },
    {
      path: '/my',
      name: 'my',
      component: my
    },
    {
      path: '/startTest',
      name: 'startTest',
      component: startTest
    },
    {
      path: '/question',
      name: 'question',
      component: question
    },
    {
      path: '/RYresult',
      name: 'RYresult',
      component: RYresult
    },
    {
      path: '/testResultRY',
      name: 'testResultRY',
      component: testResultRY
    }
  ]
})



